.products{
  max-width: $max-width;
  margin: 0 auto;
  //height: calc(100vh - var(--header-height));
  padding-left: 1em;
  padding-right: 1em;
  @include to($bp){
    min-height: auto!important;
    height: 350px;
  }
  @include from($bp){
    display: grid;
    grid-template-columns: 1fr 14rem;
    grid-template-areas: 'data' 'menu';
  }
}
.products{

}
.blanco{
  --color-tequila: var(--t-blanco)
}
.reposado{
  --color-tequila: var(--t-reposado)
}
.anejo{
  --color-tequila: var(--t-anejo)
}
.extra{
  --color-tequila: var(--t-extra)
}
.cristalino{
  --color-tequila: var(--t-cristalino)
}
.products-container{
  position: relative;
  height: calc(100vh - var(--header-height));
  display: flex;
  align-items: center;
  grid-area: data;
  @include from($bp){
    height: calc(100vh - var(--header-height))
  }
}
.products-slider{
  position: relative;

}
.product{
  display: block;
  position: absolute;
  width: 100%;
  opacity: 0;
  transition: opacity 1s;
  @include from($bp){
    display: grid;
    grid-template-columns: 45% 1fr;
  }
  &.show{
    opacity: 1;
  }
  &-banner{
    margin-bottom: 1em;
    margin-top: 5em;
    @include from(m){
      margin-top: 0;
    }
  }
 
  .banner-img{
    width: 80%;
    max-width: 600px;
    background: #E0D3CB;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    border-radius: 50%;
    img{
      transform: translateX(25%);
      display: block !important;
      max-width: 100%;
      max-height: 110%;
      @include from(m){
      width: auto !important;
      height: 110% !important;
      object-fit: cover !important;
      }
    }
  }
  &-data{
    text-align: left;
    h3{
      font-size: 1.7em;
      text-transform: uppercase;
      margin: .5rem auto;
      text-align: left !important;
    }
    @include from(lg){
      padding-right: 2em;
      font-size: 1.1em;
    }
  }
}
.data{
  &__title{
    font-family: $abraham !important;
    font-size: 2.3em;
    text-transform: uppercase;
    text-align: left !important;
    margin-left: 2rem;
    letter-spacing: 5px;
    display: table;
    @include to($bp){
      margin-left: 0;
      font-size: 1.8em;
    }
  }
  &__subtitle{
    font-family: $baskerville !important;
    font-size: 1.3em;
    color: var(--color-tequila);
    text-align: left !important;
    padding-left: 2rem;
    @include to(lg){
      padding-left: 0;
    }
  }

  &__text{
    @extend %list-style;
    text-align: left;
    margin-top: 2em;
    padding-right: 6em;
    padding-left: 2rem;
    font-family: $baskerville !important;
    li, strong{
      margin-bottom: .5em;
      font-family: $baskerville !important;
      font-size: 1.1em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    strong{
      font-size: 1em;
    }
    @include to(m){
      padding-right: 0 !important;
      padding-left: 0 !important;
    }
  }
  &__footer{
    margin-top: 1em;
    p{
      font-size: 1.7em;
      font-family: $abraham !important;;
      color: var(--color-tequila);
      text-transform: uppercase;
      --before-size: 2.5rem;
    }
  }
}
.highlight{
  display: table;
  margin: 0 auto;
  position: relative;
  &.hl-title{

  }
  &::before, &::after{
    content: '';
    position: absolute;
    display: block;
    height: 2px;
    width: var(--before-size);
    background: var(--color-tequila);
    left: calc((var(--before-size) * -1) - .3em);
    top: 50%;
  }
  &::after{
    left: calc( 100% + .3em);
  }
}
.side-bar{
  position: relative;
  z-index: 900;
  @include from($bp){
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 1.5em
  }
}
.products-menu{
  @extend %list-style;
  display: flex;
  @include to($bp){
    flex-wrap: wrap;
    justify-content: center;
  }
  @include from($bp){
    flex-direction: column;
    flex-wrap: nowrap;
    position: relative;
    margin-top: 0;
    &::before{
      content: '';
      position: absolute;
      width: 2px;
      height: 120%;
      left: -4em;
      top:-10%;
      background: var(--color-main);
      opacity: .75;
    }
  }
  li{
    position: relative;
    text-transform: uppercase;
    font-family: $athelas;
    &.show{
      span{
        display: inline-block;
      }
    }
    span{
      position: absolute;
      left: 0;
      top: 1px;
      display: none;
      @include from($bp){
        left: -2.5em;
      }
    }
    @include to($bp){
      width: calc(33% - 1em);
      margin-right: .5em;
      text-align: center;
      line-height: 2em;
      position: relative;
      &::after{
        content: '';
        position: absolute;
        height: 2px;
        width: 100%;
        left: 0;
        bottom: 0;
        background: var(--color-tequila);
      }

      &:nth-child(4){
        width: 33%;
      }
      &:nth-child(-n+3){
        margin-bottom: .5em;
      }
    }
    @include from($bp){
      margin-bottom: 3em;

      cursor: pointer;
      &:last-child{
        margin-bottom: 0;
      }
      &.show{
        span{
          display: inline-block;
        }
      }
      &::before{
        $size: 1em;
        content: '';
        position: absolute;
        display: block;
        width: $size;
        height: $size;
        top: 2px;
        left: $size * -1.5;
        background: var(--color-tequila);
        border-radius: 50%;
      }

    }
    // background: transparent;
    // margin-right: .5em;
    // text-transform: uppercase;
    // line-height: 3em;
    // padding: 0 1em;
