  }
}
.agave{
  text-align: center;
  transform: translateY(-.3em);
}
.product.blanco {
  @include to(lg){
    margin-top: -1em !important;
  }
}
.product.reposado{
  @include to(lg){
    margin-top: 2em!important;
  }
}
.product.anejo{
  @include to(lg){
    margin-top: -1em!important;
  }
}
.product.extra{
  @include to(lg){
    margin-top: 1em!important;
    
  }
}
.product.cristalino{
  @include to(lg){
    margin-top: 3em!important;
  }
}
#hl{
  margin-left: 2rem;
  @include to($bp){
    margin-left: 0;
  }
}
.hl-title{
  $size: 3rem;
  font-family: Abraham Lincoln,serif;
  letter-spacing: 3px;
  display: table;
  position: relative;
  padding: 0 2em;
  font-size: 1.7em;
  margin: auto;
  font-weight: bold;
  text-transform: uppercase;
  @include to($bp){
    font-size: 1.5em;
  }
  &::before, &::after{
    content: "";
    display: inline-block;
    position: absolute;
    width: 3rem;
    height: 3px;
    background: var(--color-tequila);
    top: 50%;
    /* left: -1rem; */
    letter-spacing: 3rem;
    // transform: translateY(-50%)
  }
  &::after{
    right: -1rem;
  }
  &::before{
    left: -1rem;
  }
}
.products-container{
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    position: absolute;
    opacity: 0;
    transition: opacity .5s;
    &.show{
      opacity: 1;
    }
  }
}
#extra{
  transform: scale(1.02) translateX(20px);
}
