
$prototype        : false;         // Activa los estilos de prototipado
$dev              : false;        // Activa los estilos para debuggin
$l-unit           : .5rem;        // Unidad básica de layout (para separaciones verticales, margin y padding)
$max-width        : 1400px;       // Ancho máximo de los contenedores ed-container y ed-grid
$gap              : $l-unit * 4;  // Separación entre columnas (2rem)
$legacy           : false;        // Soporte para versiones antiguas
$container        : ed-container; // Clase para el contenedor (core flexbox)
$item             : ed-item;      // Clase para los items (core flexbox)
$max-grid-columns : 12;           // Máximo número de columnas (core css grid)
$fractions        : 3 6;          // Fracciones para crear columnas (core flexbox)

// Config
@import "config/functions";
@import "config/variables";
@import "config/mixins";

// Theme (colors and fonts)
@import "theme/typography";

//@import "~ed-grid/ed-grid";
@import "ed-grid/ed-grid";

// Base and elements (generic)
@import "base/base";


// LAYOUT
  // Escribe solo los estilos de layout (division en regiones del viewport)
@import "layout/layout";

// MODULOS (SMACSS) O COMPONENTES (ITCSS)
  // Son los elementos de UI de tu interfaz, como botones, tablas, menus, etc.
  // Crea los archivos adicionales que necesites (uno por componente)
@import "components/buttons";
@import "components/menus";
@import "components/header";
@import "components/footer";
@import "components/age-gate";
@import "components/start";
@import "components/products";
@import "components/pages";
@import "components/contact";

// Utilities (hacks)
  // Todos los hacks que hayas necesitado para solucionar conflictos
  // o resolver situaciones extremas. Se espera poder mejorarlos luego.
@import "hacks/hacks";

.bottles{
  max-width: $max-width;
  img{
    display: block;
    width: auto;
    height: 60vh;
  }
}


.age-gate{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  .header-img{
    width: 80%;
    max-width: 500px;
    height: auto;
    margin-bottom: 1.5rem;
  }
  .footer-img{
    width: 80%;
    max-width: 465px;
    height: auto;
  }
  input{
    background: transparent;
  }
  .form-inputs{
    width: 302px;
    height: 62px;
    background: url(../img/form-img-movil.png) no-repeat;
    margin-bottom: 1.5rem;
    @include from(m){
      width: 455px;
      height: 95px;
      background: url(../img/form.png) no-repeat;
    }
  }
  .form-inputs{
    display: flex;
  }
  .ig{
    display: flex;
    margin-top: .5em;
    @include from(m){
      margin-top: 1em;

    }
    input{

      --width: 24px;
      @include from(m){
        --width: 36px;
      }
      width: var(--width);
      height: 55px;
      @include from(m){
        height: 76px;

      }
      border: none;
      font-size: 2em;
      text-align: center;
      &:focus{
        outline: none;
      }
      &.year-input, &.month-input, &.day-input{
        text-align: left;
        letter-spacing: calc(var(--width) - 5.5px);
        padding-left: calc( var(--width) / 2);
        @include from(m){
          letter-spacing: var(--width);
          padding-left: calc( var(--width) / 2 + 2px);
        }
      }
      &.year-input{
        width: calc(var(--width) * 6);
        
      }
      &.month-input, &.day-input{
        width: calc(var(--width) * 3 + 1px);
        @include from(m){
          width: calc(var(--width) * 3 + 2px);
        }
        margin-left: 2px;
        @include from(m){
          margin-left: 3px;
        }
      }
      &.month-input{
        
      }
      &.day-input{
        margin-left: -1px;
        @include from(m){
          margin-left: 4px
        }
      }
    }
  }
  .year {
    margin-right: 5px;
  }
  .month{
    margin-right: 6px;
  }
  .input-send{
    display: block;
    margin: 0 auto;
    cursor: pointer;
    visibility: hidden;
    &.show{
      visibility: visible;
    }
  }
  .form{
    margin-bottom: 2.5rem;
  }
  .cy{
    display: none;
  }
}
