.start{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding-top: var(--header-height);
}
.bottles{
  width: 100%;
}
.bottles{
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  @include to(m){
    width: $max-width;
    margin: 0 auto;
    flex-wrap: wrap;
    transform: scale(.7) translateY(-4em);
    max-height: calc(100vh - var(--header-height));
    padding: 0;
    .bottle-container{
      width: 33%;
      height: auto;
      img{
        width: auto;
        height: calc(100vh - var(--header-height));
        object-fit: cover;
        transform: scale(.85);
        @include to(m){
          width: 100% !important;
          height: auto !important;
        }
      }
    }
  }
}
.bottle-container{
  @include from(m){
    margin-right: .5em;
    &:last-child{
      margin-right: 0;
    }
  }
  @include from(lg){
    margin-right: .5em;
    &:last-child{
      margin-right: 0;
    }
  }
}
