.form-age-gate{
  width: calc(var(--form-size) * 8 + 1em);
  margin: 2em auto ;
}
.inputs-container{
  display: flex;
  margin-bottom: 2em;
  input{
    display: block;
    width: 100%;
    height: var(--form-size);
    line-height: var(--form-size);
    font-size: calc(var(--form-size) * .6);
    text-align: center;
    letter-spacing: .5rem;
    border: var(--border)
  }
  .text-form{
    text-align: center;
    font-size: .6em;
    margin-bottom: .5rem;
    @include from(m){
      font-size: .7rem;
    }
  }
}
.btn-container{
  display: flex;
  justify-content: center;
  height: 4em;
}
.btn-age-gate{
  width: 10em;
  line-height: 4em;
  border: var(--border);
  background: transparent;
  cursor: pointer;
  display: none;
  &:focus{
    outline: none
  }
  &.show{
    display: block
  }
  img{
    width: auto;
    max-height: 60px;
  }
}

.group-form{
  width: calc(var(--form-size) * 2);
  margin-right: .5em;
  &:last-child{
    margin-right: 0;
  }
  &.year{
    width: calc(var(--form-size) * 4);
  }
}

.spin{
  animation: spin 1s linear infinite;
  @keyframes spin {
    turn{
      transform: rotate(360deg)
    }
  }
}
.full-screen{
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background: url(../img/bg.jpg) repeat;
  z-index: calc(var(--z-modal) * 100);
  padding: 1em;
  &__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
  }
  &__logo{
    display: block;
    width: 150px;
    margin: 0 auto;
    @include from(l){
      width: 250px;
    }
  }
  &__title, &__text{
    font-size: 1.4em;
    text-align: center;
    margin: .5rem 0 !important;
    @include from(l){
      margin: 1rem 0 0 !important;
    }
  }
  &__title{
    font-family: var(--font-accent), 'sans-serif';
    color: var(--color-main);
    font-size: var(--modal-title-size);
    margin-bottom: 1em;
  }
  &__text{
    font-size: .8em;
    color: #95999c;
    &:last-child{
      color: var(--color-font);
    }
  }
}
