.sction-page{
  padding-left: 1rem;  
  padding-right: 1rem; 
  max-width: 1400px;
  margin: 0 auto; 
  @include from(m){
    padding-left: 3rem;  
    padding-right: 3rem;  
    display: grid;
    grid-template-columns: 1fr 420px;
    grid-gap: 2rem;
    align-items: center;
  }
  h3{
    margin-bottom: .5rem;
  }
  p{
    text-align: justify;
  }
  .escudo, .sello{
    display: block;
    margin: 1rem auto 0;
    width: 60%;
    height: auto;
  }
  .sello{
    width: 70px;
  }
}

.separator{
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 50%;
  height: 1px;
  background: var(--color-font);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
