.contact{
  max-width: $max-width;
  margin: 0 auto;
  min-height: calc(100vh - var(--footer-height));
  padding-left: 1em;
  padding-right: 1em;
  @include from($bp){
    display: grid;
    grid-template-rows: 375px 1fr;
  }
  @include to(m){
    //margin-top: 13em !important;
  }
  h2{
    text-transform: uppercase;
  }
  &__form{
    @include from($bp){
      max-width: calc(100% - 1em) !important;
    }
  }
  
  .social{
    @extend %list-style;
    margin-top: auto;
    display: flex;
    justify-content: center;
    transform: scale(.7);
    @include from(lg){
      // padding-right: 11em;
      transform: scale(1);
    }
    li{
      margin-right: 2rem;
      font-size: .8em;
      &:last-child{
        margin-right: 0;
      }
      &.instagram{
        a:hover{
          color: #c32aa3;
        }
      }
      &.facebook{
        a:hover{
          color: #3b5998;
        }
      }
      &.twitter{
        a:hover{
          color: #1da1f2;
        }
      }
    }
    a{
      font-size: 1.5em;
      transition: color .3s;
    }
  }
  &__container{
    position: relative;
    z-index: var(--z-normal);
    @include to(m){
      top: 12rem;
    }
    @include from($bp){
      display: grid;
      grid-template-columns: 35% 1fr;
      grid-gap: 3em;
      padding-left: 1em;
      padding-top: 3em;
    }
  }
  &__info{
    @include to($bp){
      margin-bottom: 1em;
      h2{
        margin-bottom: 1rem;
      }
      p{
        margin-bottom: 1.5em;
      }
    }
    @include from($bp){
     display: flex;
     flex-direction: column;
     position: relative;
     &::after{
       content: '';
       position: absolute;
       display: block;
       height: 100%;
       background: var(--color-font);
       width: 1px;
       right: -3em;
     }
     h2{
       margin-bottom: 2rem;
       font-family: var(--font-second);
       margin-left: 3rem;
      font-size: 2em;
     }
     p{
       width: 60%;
       margin-left: 3rem;
     }
    }
  }
  &__form{
    padding-left: 6rem;
    @include to($bp){
      padding-left: 0;
      margin-bottom: 13em;
    }
    input, label, textarea{
      font-family: var(--font-main);
      font-size: 1em;
      resize: none;
      @include from($bp){
      }
    }
    input, textarea{
      background: none;
      border: none;
      &:focus{
        outline: none;
      }
    }
    input, label{
      line-height: var(--line-hieght-input);
      //border-bottom: var(--border)
    }

    label{
      text-transform: uppercase;
    }
    textarea{
      height: calc(var(--line-hieght-input) * 5);
      line-height: var(--line-hieght-input);
      border-bottom: none !important;
    }
    .input-group{
      display: grid;
      grid-template-columns: 75px 1fr;
      margin-bottom: 1em;
      border-bottom: var(--border)
    }
    .textarea{
      position: relative;
      label{
        border-bottom: none;
      }
      label, textarea{
        position: relative;
        z-index: 100;

      }
      &::before{
        // @mixin lines($color, $stop, $n){
        //   $l-gra
        // }
        $color: grey;
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 1px;
        background: $color;
        top: var(--line-hieght-input);
        left: 0;
        box-shadow: blocknotes($color,2em,4);

      }
    }
    @include to($bp){

    }
    @include from($bp){

    }
  }
}
.form__footer{
  --footer-form-space: 30%;
  display: grid;
  grid-template-columns: var(--footer-form-space) 1fr;
  grid-gap: 1em;
  @include to($bp){

  }
  @include from($bp){

  }
  button{
    border: none;
    color: #fff;
    background: #111;
    height: 3em;
    cursor: pointer;
    &.disabled{
      color: #darkgray;
      background: grey;
      height: 3em;
      cursor: initial;
    }
  }
}
.success{
  p{
    color: darkgreen
  }
}
.error{
  p{
    color: darkred
  }
}
.contact__footer{
  display: flex;
  padding-top: 2em;
  &-container{
    margin-top: auto;
    display: grid;
    width: 100%;
    grid-template-columns: repeat(5,20%);
    .img,.social-network{
      display: block;
    }
    .social-network{
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.5em;
      text-transform: uppercase;
      img{
        max-width: 21.218px;
        max-height: 21.218px;
      }
      &.comunity{
        font-size: 65%;
        @include from(m){
          font-size: 80%;
        }
        @include from(lg){
          font-size: 95%;
        }

      }
    }
    img{
      display:block;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  .square{
    width: 100%;
    //max-height: 180px;
    // min-height: calc(#{$max-width} / 5);
    background: #fff;
  }
}

input:-internal-autofill-previewed, input:-internal-autofill-selected, textarea:-internal-autofill-previewed, textarea:-internal-autofill-selected, select:-internal-autofill-previewed, select:-internal-autofill-selected{
  background-color: transparent !important;
}
.contact-img{
  max-width: 320px;
  display: block;
  height: auto;
  margin: 0 auto
}
.contact__info{
  // background: lime;
}
.textarea{
  border-bottom-color: transparent !important;
}
