// Typography
$font-family: 'sans-serif';
$bp: lg;
$margin-top: 1.5em;
$miryad : 'Myriad Pro Regular', serif;
$abraham : 'Abraham Lincoln', serif;
$baskerville: 'Baskerville', serif ;
$athelas: 'Athelas', serif;
:root {
  --color-main: rgba(186, 146, 124, 1);
  --color-main-opacity: rgba(186, 146, 124, .5);
  --t-blanco: #053CCF;
  --t-reposado: #F95F26;
  --t-anejo: #FA0122;
  --t-extra: #610001;
  --t-cristalino: #010101;
  --color-font: #444;
  --miryad : 'Myriad Pro Regular', serif;
  --abraham : 'Abraham Lincoln', serif;
  --baskerville: 'Baskerville', serif ;
  --athelas: 'Athelas', serif;
  --font-main: 'Myriad Pro Regular', serif;
  --font-accent: 'Abraham Lincoln', serif;
  --font-titles: 'Baskerville', serif ;
  --font-second: 'Athelas', serif;
  --size: 2em;
  --header-height: 100px;
  --footer-height: 50px;
  --z-back    : -10;
  --z-normal  : 1;
  --z-tooltip : 10;
  --z-banner: 75;
  --z-banner-c: 90;
  --z-fixed   : 100;
  --z-modal   : 1000;
  --z-age : 10000;
  --form-size: 1.8rem;
  --line-hieght-input: 2em;
  --border: 1px solid gray;
  --before-size: 2.5em;
  @media (min-width: 768px){
    --size: 2.4em;
    --form-size: 3rem;
  }
  --modal-title-size: 2.5em;
  @media (min-width: 768px){
    --modal-title-size: 3em;
  }
  //@media screen and(max-width: 1023px) {
  //  --header-height : 150px;
  //}
}
