header{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  height: var(--header-height);
  // background: url(../img/bg.jpg) repeat;
  background: #F4F4F4;
  z-index: 10000;

}
.Header-container{

  max-width: $max-width;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--header-height);
  padding-left: 1em;
  padding-right: 1em;
  img{
    width: auto;
    height: calc(var(--header-height) * .5);
  }
}
.toggle-nav{
  $size: 1.5rem;
  $height: 4px;
  width: $size;
  height: calc(#{$height * 3} + 1px);
  position: relative;
  cursor: pointer;
  @include to(lg){
    //transform: translateX(-3em);
  }
  &::after, &::before{
    border-radius: 2px;
    content: '';
    position: absolute;
    height: $height;
    width: 100%;
    left: 0;
    background: var(--color-main);
  }
  &::after{
    bottom: 0;
  }

}
.navigator{
  position: fixed;
  display: flex;
  transform: translateX(100%);
  //justify-content: space-between;
  top: 0;
  right: 0;
  flex-direction: column;
  z-index: var(--z-fixed);
  width: 100%;
  height: 100vh;
  background: rgba(#BA927C,.9);
  color: #fff;
  padding: 2.5rem;
  transition: transform .5s;
  @include from(m){
    max-width: 320px;
  }
  &.show{
    transform: translateX(0);
  }
  .close-menu{
    $size: 20px;
    height: $size;
    width: $size;
    align-self: flex-end;
    margin-top: 0;
    margin-right: 0;
    margin-bottom: 10vh;
    cursor: pointer;
    //background: darkred;
    background: #C09C88;
    img{
      opacity: .75;
    }
  }
}
.branding{
  //max-width: 80%;
  //margin: auto auto 1.5em;
  margin-top: auto;
  img{
    display: block;
    width: 62px;
    margin: 0 auto 1rem;
  }
  h1{
    text-align: center;
    font-size: .6em;
    font-weight: normal;
  }
}
.top-menu{
  $separator: 10vh;
  @extend %list-style;
  li{
    text-align: right;
    position: relative;
    margin-bottom: $separator;
    &:last-child{
      margin-bottom: 0;
      &:before, &::after {
        display: none;
      }
    }
    &:before, &::after{
      content: '';
      display: block;
      position: absolute;
      width: 1px;
      height: calc(#{$separator} - 0em);
      top: 1em;
      right: 0;
      background: red;
    }
    &::before{
      background: rgba(#fff,5%);
    }
    &::after{
      width: 2px;
      background: linear-gradient(transparent 80%, #fff 75%);
      transform: translateX(25%);
    }
  }
  a{
    text-transform: uppercase;
    opacity: .75;
  }
}

